.table-container-wrapper {
 position: relative;
}

.board-buttons-container {
 display: flex;
 justify-content: flex-start;
 margin: 30px 4% -10px 4%;
}

.board-button {
 background: none;
 border: none;
 padding: 10px 15px;
 margin: 5px;
 font-size: 16px;
 cursor: pointer;
 color: black;
 transition: all 0.3s ease;
 font-weight: bold;
}

.board-button:hover, .board-button.selected {
 color: #B93234;
 font-size: 18px;
}

.board-buttons-container button {
 padding: 10px 20px;
 margin: 5px;
 font-size: 16px;
 cursor: pointer;
 gap: 10px;
}

.table-container {
 height: 600px;
 overflow-y: auto;
 margin: 10px auto;
 width: 90%;
 border: 1px solid #ddd;
 padding: 10px;
}

table {
 width: 100%;
 border-collapse: collapse;
}

th, td {
 padding: 10px;
 border: 1px solid #ddd;
 text-align: left;
}

th {
 background-color: #f4f4f4;
 font-weight: bold;
}

.clickable-row {
 cursor: pointer;
}

.clickable-row:hover {
 background-color: #f1f1f1;
}

.selected-row-details {
 margin-top: 20px;
 padding: 20px;
 border: 1px solid #ddd;
 background-color: #f9f9f9;
}

.button-container {
 display: flex;
 justify-content: flex-end;
 margin-right: 7%;
}

.add-button {
 background: none;
 border-radius: 5px;
 border-width: 1.5px;
 font-weight: bold;
 padding: 10px 20px;
 margin: 5px;
 font-size: 12px;
 cursor: pointer;
 transition: all 0.3s ease;
}

.add-button:hover {
 transform: scale(1.03);
}

/*스크롤바 스타일링*/
.table-container::-webkit-scrollbar {
 width: 8px; /* 스크롤바 너비 */
}
.table-container::-webkit-scrollbar-track {
 background: #f1f1f1;
 border-radius: 10px;
}
.table-container::-webkit-scrollbar-thumb {
 background: #888;
 border-radius: 10px;
}
.table-container::-webkit-scrollbar-thumb:hover {
 background: #555;
}